import axios from "axios";
import { useState } from "react";
import { Button, Card, FloatingLabel, Form, Modal, Row } from "react-bootstrap";


export default function Gallery(){
    
    const [FormShow,setFormShow] =useState()
    return(
        <>
            <h3> .</h3>
            <button onClick={()=>{
               setFormShow(true)
            }}></button>
            <Row>

            </Row>
            <UploadFormModal show={FormShow} setShow={setFormShow}/>
        </>
    )
}



function UploadFormModal(props) {
    
    //  
    const [caption, setCaption]=useState("");
    //   
    const [image, setImage]=useState(null);
    //  preview  state
    const [previewImage, setPreviewImage]=useState(null);

    //   
    const handleChange=(e)=>{
        //  
        const file = e.target.files[0];
        console.log(file);
        setImage(file);
        //    
        const reader=new FileReader();
        reader.readAsDataURL(file);
        reader.onload=(event)=>{
          //    
		  const data=event.target.result;
          setPreviewImage(data);
        };
    }

    const handleUpload = ()=>{
        //FormData    caption  image   
        const formData=new FormData();
        formData.append("caption", caption);
        formData.append("image", image);
        axios.post("/gallery", formData, {
            headers:{"Content-Type":"multipart/form-data"}
        })
        .then(res=>{
            console.log(res.data);  
            props.onClose();
        })
        .catch(error=>{
            console.log(error);
        });
    }

    return (
      <Modal
        {...props}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
        onHide={ ()=>{
            //props    Modal 
            props.setShow(false)
            
        }}
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
              
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <FloatingLabel controlId="floatingInput" label=" " className="mb-3">
            <Form.Control onChange={(e)=>setCaption(e.target.value)} name="caption" type="text"  placeholder=" "/>
          </FloatingLabel>
          <FloatingLabel  controlId="floatingPassword" label=" " className="mb-3">
            <Form.Control onChange={handleChange} name="image" type="file" accept="image/*" placeholder=" " />
          </FloatingLabel>
          <Card style={{ width: '18rem' }}>
            <Card.Img variant="top" src={previewImage} />
            <Card.Body>
              <Card.Text>{caption}</Card.Text>
            </Card.Body>
          </Card> 
        </Modal.Body>
        <Modal.Footer>
          <Button variant="primary" onClick={handleUpload}></Button>
        </Modal.Footer>
      </Modal>
    );
  }