// src/pages/Member.js


import axios from "axios";
import { useEffect, useState } from "react";

import { Link, NavLink, useNavigate } from "react-router-dom";

import AddIcon from '@mui/icons-material/Add';
import Button from '@mui/material/Button';
import DeleteIcon from '@mui/icons-material/Delete';
import { Fab, Table, TableBody, TableCell, TableHead, TableRow } from "@mui/material";



export default function Member() {
  //    
  const [members, setMembers] = useState([])

  const refresh = () => {
    axios.get("/members")
      .then(res => {
        //   state 
        setMembers(res.data)
      })
      .catch(error => {
        console.log(error)
      })
  }

  //Member Component      
  useEffect(() => {
    refresh()
  }, [])
  //     
  const handleDelete = (num) => {
    axios.delete("/members/" + num)
      .then(res => {
        // refresh
        console.log(res.data)
        if (res.data.isSuccess) {
          refresh()
        }

      })
      .catch(error => console.log(error))
  }

  //   
  const navigate = useNavigate()
  return (
    <>


      <Fab size="small" color="secondary" aria-label="add">
        <AddIcon onClick={()=>{navigate("/members/new")}} />
      </Fab>
    
      <h1></h1>
      <Table>
          <TableHead>
            <TableRow>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
              <TableCell></TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
          {members.map(item => <tr key={item.num}>
            <TableCell component="th" scope="row">{item.num}</TableCell>
            <TableCell align="left">{item.name}</TableCell>
            <TableCell align="left">{item.addr}</TableCell>
        
            <TableCell>
              <button onClick={() => {
                navigate(`/members/${item.num}/edit`)
              }}></button>
              {/* link */}
              {/* <Link to={`/members/${item.num}/edit`}></Link> */}
            </TableCell>
            <TableCell>
              <Button variant="outlined" startIcon={<DeleteIcon /> } onClick={() => handleDelete(item.num)}></Button>
          
            </TableCell>
          </tr>)}
        </TableBody>  
      </Table>
    </>
  )
}