import { NavLink, Route, Routes } from "react-router-dom";
import Home from "./Pages/Home";
import Member from "./Pages/Member";
import MemberForm from "./Pages/MemberForm";
import { Alert, Button, FloatingLabel, Modal, Form } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { useEffect, useState } from "react";
import { decodeToken } from "jsontokens";
import 'bootstrap/dist/css/bootstrap.css'
import MemberUpdateForm from "./Pages/MemberUpdateForm";
import BsNavbar from "./components/BsNavBar";
import EditorComponent from "./Pages/EditorComponent";
import Gallery from "./Pages/gallery";


// component
function App() {
  const dispatch = useDispatch()
  //  token !
  useEffect(()=>{
      if(localStorage.token){
          // 
          const result=decodeToken(localStorage.token);
          //
          const expTime=result.payload.exp*1000; // *1000   ms   
          //
          const now=new Date().getTime();
          //    
          if(expTime < now){
              dispatch({type:"SET_LOGIN", payload:false})
          }else{//      !
              dispatch({type:"SET_LOGIN", payload:true})
              dispatch({type:"UPDATE_USER", payload:result.payload.sub})
              //axios header        .
              axios.defaults.headers.common["Authorization"]="Bearer+"+localStorage.token
          }
      }
  }, [])
  // (   )  
  const isLogin = useSelector(state => state.isLogin)
  
  return (
    <>
    <BsNavbar/>
    <div className="container">
      <ul>
        <li><NavLink to="/">Home</NavLink></li>
        <li><NavLink to="/members">Member</NavLink></li>
      </ul>

      <Routes>
        <Route path="/" element={<Home/>}/>
        <Route path="/members" element={<Member/>}/>
        <Route path="/members/new" element={<MemberForm/>}/>
        <Route path="/members/:num/edit" element={<MemberUpdateForm/>}/>
        <Route path="/editor" Component={EditorComponent}/>
        <Route path="/gallery" Component={Gallery}/>
      </Routes>

      <LoginModal show={!isLogin}></LoginModal>
    </div>

    </>
  );
}


// App.js  import  App   . (src/index.js)
export default App;

function LoginModal(props) {
  // userName  password   
  const [state, setState]=useState({})
  const [isError, setError]=useState(false)

  //input      
  const handleChange = (e)=>{
      setState({
          ...state,
          [e.target.name]:e.target.value
      })
  }
  const dispatch=useDispatch()
  //     
  const handleSubmit = ()=>{
      axios.post("/auth", state)
      .then(res=>{
          //    JWT   
          console.log(res.data)
          // localStorage  
          localStorage.token=res.data
          // 
          dispatch({type:"SET_LOGIN", payload:true})
          //  
          setError(false)
          //    
          const result=decodeToken(localStorage.token)
          //  (subject) 
          const userName=result.payload.sub

          //userName   dispatch
          dispatch({type:"UPDATE_USER", payload:userName})

          //axios header        .
          axios.defaults.headers.common["Authorization"]="Bearer+"+localStorage.token
      })
      .catch(error=>{ 
          //     .
          setError(true)
      })
  }
  return (
    <Modal 
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered>
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
            .
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <FloatingLabel controlId="floatingInput" label="User Name" className="mb-3">
          <Form.Control onChange={handleChange}  name="userName" type="text"  placeholder="User Name"/>
        </FloatingLabel>
        <FloatingLabel  controlId="floatingPassword" label="Password" className="mb-3">
          <Form.Control onChange={handleChange} name="password" type="password" placeholder="Password" />
        </FloatingLabel>
        {
          isError && <Alert variant='danger'>    </Alert>
        }
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={handleSubmit}></Button>
      </Modal.Footer>
    </Modal>
   
  );
}


