
import { Button } from "@mui/material"
import axios from "axios"
import { useState } from "react"
import { Col, Form, Row } from "react-bootstrap"
import {  useNavigate } from "react-router-dom"

//src/pages/MemberForm.js
export default function MemberForm(){
    //     
    const navigate =useNavigate()
    
    //   state 
    const[state,setState]=useState([])
    //handleChange      .
    const handleChange=(e)=>{
        setState({
            ...state,
            [e.target.name]:e.target.value
        })
    }
    //   json 
    const handleSave =()=>{
        //state object  
        axios.post("/members",state)
        .then(res=>{
            if(res.data.isSuccess){
                alert(" ");
                //  (javascript  , useNavigate() Hook )
                navigate("/members")
            }
        })
    }

    return(
        <>
            <h2>   </h2>
            <input type="text" name="name" placeholder="..." onChange={handleChange} />
            <input type="text" name="addr" placeholder="..." onChange={handleChange}/>
            <button onClick={ handleSave}></button>

            <Form>
                <Form.Group as={Row} className="mb-3" controlId="name">
                    <Form.Label column sm="2"></Form.Label>
                    <Col sm="10">
                        <Form.Control type="text" placeholder=" ..."/>
                    </Col>
                </Form.Group>
                <Form.Group as={Row} className="mb-3" controlId="addr">
                    <Form.Label column sm="2"></Form.Label>
                    <Col sm="10">
                        <Form.Control type="text" placeholder=" ..." />
                    </Col>
                </Form.Group>
                <Button variant="outlined" onClick={handleSave}></Button>
            </Form>
        </>
    )
}